<template>
  <div>
    <div class="header">
      <div>
        <!-- <van-icon name="arrow-left" size="22" /> -->
      </div>
      <div>{{$t('login.create')}}</div>
      <div @click="show=true">{{lang}}</div>
    </div>
    <main>
      <div class="content">
        <div class="two_header">{{$t('login.name')}}</div>
        <input type="text" :placeholder="$t('login.inputName')" v-model.trim="nickname" />
        <div class="two_header">{{$t('login.setUp')}}</div>
        <input type="password" :placeholder="$t('login.inputPass')" v-model.trim="password" />
        <div class="two_header">{{$t('login.surePass')}}</div>
        <input type="password" :placeholder="$t('login.inputSure')" v-model.trim="rePassword" />
        <div class="two_header">{{$t('other.tuiCode')}}</div>
        <input type="text" :placeholder="$t('other.tui')" v-model.trim="tjcode" />
        <div class="btn" @click="sure">{{$t('login.create')}}</div>
      </div>
    </main>
    <van-action-sheet
      v-model="show"
      :actions="actions"
      :cancel-text="$t('home.cancel')"
      :description="$t('user.chooseLang')"
      close-on-click-action
      @select="onSelect"
    />
  </div>
</template>

<script>
export default {
  data() {
    return {
      nickname: '',
      password: '',
      rePassword: '',
      tjcode: '',
      lang: '中文简体',
      show: false,
      actions: [
        { name: '中文简体', id: 1 },
        { name: '中文繁體', id: 2 },
        { name: 'English', id: 3 },
      ],
      type: 'zh-cn',
    }
  },
  methods: {
    sure() {
      if (this.nickname == '') {
        this.$toast(this.$t('login.inputName'))
      } else if (this.password == '') {
        this.$toast(this.$t('login.inputPass'))
      } else if (this.password != this.rePassword) {
        this.$toast(this.$t('other.both'))
      } else if (this.tjcode == '') {
        this.$toast(this.$t('other.tui'))
      } else {
        this.$toast.loading({
          duration: 60000,
          forbidClick: true,
          message: this.$t('other.loading'),
          loadingType: 'spinner',
        })
        this.$post('/login/new_register', {
          nickname: this.nickname,
          password: this.password,
          confirm_password: this.rePassword,
          tjcode: this.tjcode,
        }).then((res) => {
          console.log(res)
          if (res.code == '1') {
            this.$toast(this.$t('other.succes'))
            localStorage.setItem('zhuji', JSON.stringify(res.data.zhuji))
            localStorage.setItem('id', res.data.id)
            this.$router.push('/backups')
          } else {
            this.$toast(res.msg)
          }
        })
      }
    },
    // 选择语言
    onSelect(item) {
      if (item.id == 1) {
        this._i18n.locale = 'zh'
        localStorage.setItem('lang', 'zh')
        this.type = 'zh-cn'
        this.lang = '中文简体'
        this.changeLang(this.type)
      } else if (item.id == 2) {
        // 中文繁体
        this._i18n.locale = 'fa'
        localStorage.setItem('lang', 'fa')
        this.type = 'zh-hk'
        this.lang = '中文繁體'
        this.changeLang(this.type)
      } else if (item.id == 3) {
        // 英语
        this._i18n.locale = 'en'
        localStorage.setItem('lang', 'en')
        this.type = 'en-gb'
        this.lang = 'English'
        this.changeLang(this.type)
      }
    },
    changeLang(type) {
      this.$toast.loading({
        duration: 60000,
        forbidClick: true,
        message: this.$t('other.loading'),
        loadingType: 'spinner',
      })
      this.$get('/guanwang/Languagetypes', {
        lan_type: type,
      }).then((res) => {
        this.$toast.clear()
        if (res.code == 1) {
        } else {
        }
      })
    },
  },
  created() {
    this.tjcode = this.$route.query.tjcode
    let lang =
      localStorage.getItem('lang') == (undefined || '' || null)
        ? 'zh'
        : localStorage.getItem('lang')
    switch (lang) {
      case 'zh':
        this.lang = '中文简体'
        break
      case 'fa':
        this.lang = '中文繁體'
        break
      case 'en':
        this.lang = 'English'
        break
    }
  },
}
</script>

<style lang="less" scoped>
.header {
  display: flex;
  align-items: center;
  box-sizing: border-box;
  padding: 0 0.333333rem;
  height: 1.226667rem;
  background-color: #212126;
  div {
    height: 100%;
    display: flex;
    align-items: center;
  }
  div:nth-child(1) {
    width: 20%;
  }
  div:nth-child(2) {
    width: 60%;
    justify-content: center;
    font-size: 0.48rem;
  }
  div:nth-child(3) {
    width: 20%;
    justify-content: flex-end;
  }
}
main {
  height: calc(100vh - 1.226667rem);
  overflow-y: scroll;
  -webkit-overflow-scrolling: touch;
}
.content {
  box-sizing: border-box;
  padding: 0 0.333333rem;
}
.two_header {
  font-size: 0.426667rem;
  margin-top: 0.826667rem;
}
input {
  padding: 0.266667rem 0;
  box-sizing: border-box;
  border-bottom: 0.013333rem solid #2c2c2c;
  width: 100%;
  margin-top: 0.16rem;
  font-size: 0.373333rem;
}

.btn {
  width: 100%;
  text-align: center;
  height: 1.2rem;
  line-height: 1.2rem;
  color: #000000;
  font-size: 0.426667rem;
  border-radius: 0.6rem;
  background: linear-gradient(to top, #eec780, #f6dca7);
  background: -webkit-linear-gradient(to top, #eec780, #f6dca7);
  margin: 3rem auto 0.46rem;
}
// 动作面板的样式
.van-action-sheet__gap {
  background-color: #171717;
}
.van-action-sheet__cancel,
.van-action-sheet__description {
  background-color: #313338;
  color: #eac685;
}
.van-action-sheet__description::after {
  border-color: #2c2c2c;
}
.van-action-sheet__item {
  background-color: #313338;
  color: #fff;
}
</style>
